<template>
  <div class="demo-page svg-demo">
    <el-row>
      <el-col :span="24">
        <h4>注意： Internet Explorer和Safari不支持SVG滤镜！</h4>
      </el-col>
      <el-col :span="24">
        <div class="svg__wrapper">
          <svg xmlns="http://www.w3.org/2000/svg" version="1.1" class="show-border small">
            <defs>
              <filter :id="svgConfig.filterId1" x="0" y="0"  width="200%" height="200%">
                <feGaussianBlur v-bind="svgConfig.feGaussianBlur" />
              </filter>
            </defs>
            <rect width="90" height="90" stroke="green" stroke-width="3" fill="yellow" :filter="`url(#${svgConfig.filterId1})`" />
          </svg>
        </div>
        <div class="svg__wrapper">
          <svg xmlns="http://www.w3.org/2000/svg" version="1.1" class="show-border small">
            <defs>
              <filter :id="svgConfig.filterId2" x="0" y="0"  width="200%" height="200%">
                <feOffset result="offOut" in="SourceGraphic" dx="20" dy="20" />
                <feBlend in="SourceGraphic" in2="offOut" mode="normal" />
              </filter>
            </defs>
            <rect width="90" height="90" stroke="green" stroke-width="3" fill="yellow" :filter="`url(#${svgConfig.filterId2})`" />
          </svg>
        </div>

        <div class="svg__wrapper">
          <svg xmlns="http://www.w3.org/2000/svg" version="1.1" class="show-border small">
            <defs>
              <filter :id="svgConfig.filterId3" x="0" y="0"  width="200%" height="200%">
                <feOffset result="offOut" in="SourceGraphic" dx="20" dy="20" />
                <feGaussianBlur result="blurOut" in="offOut" stdDeviation="10" />
                <feBlend in="SourceGraphic" in2="blurOut" mode="normal" />
              </filter>
            </defs>
            <rect width="90" height="90" stroke="green" stroke-width="3" fill="yellow" :filter="`url(#${svgConfig.filterId3})`" />
          </svg>
        </div>

        <div class="svg__wrapper">
          <svg xmlns="http://www.w3.org/2000/svg" version="1.1" class="show-border small">
            <defs>
              <filter :id="svgConfig.filterId4" x="0" y="0"  width="200%" height="200%">
                <feOffset result="offOut" in="SourceAlpha" dx="20" dy="20" />
                <feGaussianBlur result="blurOut" in="offOut" stdDeviation="10" />
                <feBlend in="SourceGraphic" in2="blurOut" mode="normal" />
              </filter>
            </defs>
            <rect width="90" height="90" stroke="green" stroke-width="3" fill="yellow" :filter="`url(#${svgConfig.filterId4})`" />
          </svg>
        </div>

        <div class="svg__wrapper">
          <svg xmlns="http://www.w3.org/2000/svg" version="1.1" class="show-border small">
            <defs>
              <filter :id="svgConfig.filterId5" width="200%" height="200%">
                <feOffset result="offOut" in="SourceGraphic" dx="20" dy="20" />
                <feColorMatrix result="matrixOut" in="offOut" type="matrix"
                               values="0.2 0 0 0 0 0 0.2 0 0 0 0 0 0.2 0 0 0 0 0 1 0" />
                <feGaussianBlur result="blurOut" in="matrixOut" stdDeviation="10" />
                <feBlend in="SourceGraphic" in2="blurOut" mode="normal" />
              </filter>
            </defs>
            <rect x="30" y="30" width="90" height="90" stroke="green" stroke-width="3" fill="yellow" :filter="`url(#${svgConfig.filterId5})`" />
          </svg>
        </div>

        <div class="svg-config-form__wrapper">
          <el-form ref="svgConfigForm" :model="svgConfig" label-width="200px">
            <!--<el-form-item label="圆心坐标(x)">-->
              <!--<el-slider-->
                  <!--v-model="circleConfig.cx"-->
                  <!--:min="50"-->
                  <!--:max="300"-->
                  <!--:step="50"-->
                  <!--show-stops>-->
              <!--</el-slider>-->
            <!--</el-form-item>-->

            <!--<el-form-item label="圆心坐标(y)">-->
              <!--<el-slider-->
                  <!--v-model="circleConfig.cy"-->
                  <!--:min="50"-->
                  <!--:max="300"-->
                  <!--:step="50"-->
                  <!--show-stops>-->
              <!--</el-slider>-->
            <!--</el-form-item>-->

            <!--<el-form-item label="半径(r)">-->
              <!--<el-slider-->
                  <!--v-model="circleConfig.r"-->
                  <!--:min="20"-->
                  <!--:max="100"-->
                  <!--:step="10"-->
                  <!--show-stops>-->
              <!--</el-slider>-->
            <!--</el-form-item>-->

            <!--<el-form-item label="笔划颜色(stroke)">-->
              <!--<el-color-picker v-model="circleConfig.stroke" show-alpha >-->
              <!--</el-color-picker>-->
              <!--{{circleConfig.stroke}}-->
            <!--</el-form-item>-->

            <!--<el-form-item label="笔划宽度(strokeWidth)">-->
              <!--<el-slider-->
                  <!--v-model="circleConfig.strokeWidth"-->
                  <!--:max="20"-->
                  <!--:step="1"-->
                  <!--show-stops>-->
              <!--</el-slider>-->
            <!--</el-form-item>-->

            <!--<el-form-item label="填充颜色(fill)">-->
              <!--<el-color-picker v-model="circleConfig.fill" show-alpha >-->
              <!--</el-color-picker>-->
              <!--{{circleConfig.fill}}-->
            <!--</el-form-item>-->

          </el-form>
        </div>
      </el-col>
    </el-row>

  </div>
</template>

<script>
import DemoPageMixin from '@/mixins/demo-page-mixin'
import uuid from 'uuid/v4'
export default {
  name: 'SVGDemo001',
  mixins: [DemoPageMixin],
  data () {
    return {
      svgConfig: {
        filterId1: uuid(),
        filterId2: uuid(),
        filterId3: uuid(),
        filterId4: uuid(),
        filterId5: uuid(),
        feGaussianBlur: { // 元素定义模糊效果
          in: 'SourceGraphic', // 这个部分定义了由整个图像创建效果
          stdDeviation: 15 // 属性定义模糊量
        }
      }
    }
  }
}
</script>

